import { $axios } from "./app-http";

const GET_PREGNANCY_DETAILS_EDNPOINT = "pregnancy/get-details";
const CREATE_NEW_ENDPOINT = "pregnancy/create-pregnancy";

export async function getPregnancyDetails(cycleId) {
    const { data } = await $axios.get(GET_PREGNANCY_DETAILS_EDNPOINT, {
        params: { cycleId }
    });
    return data;
}

export async function createNew({ cycleId, patientId }) {
    const { data } = await $axios.post(CREATE_NEW_ENDPOINT, {
        cycleId,
        patientId
    });
    return data;
}
